const Footer = () => (
  <div className="container">
    <h2>You can also contact me here :</h2>
    <div>
      <a href="https://my-resume-aurelien.herokuapp.com/" target="_blank">
        <img src="/static/images/resume.png" alt="online cv" />
      </a>
      <a
        href="https://www.linkedin.com/in/aur%C3%A9lien-brachet-146556141/"
        target="_blank"
      >
        <img src="/static/images/linkedin-logo.png" alt="linkedin" />
      </a>
      <a>
        <img src="/static/images/instagram-logo.png" alt="instagram" />
      </a>
    </div>
    <style jsx>
      {`
        .container {
          height: 150px;
          border-top: 2px solid black;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
        a {
          cursor: pointer;
          margin-left: 15px;
          margin-right: 15px;
        }
      `}
    </style>
  </div>
);

export default Footer;



